import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingpageComponent } from './pages/landingpage/landingpage.component';
import { BookerLandingComponent } from './pages/booker-landing/booker-landing.component';
import { NewSpecialistComponent } from './users/new-specialist/new-specialist.component';
import { WelcomePageComponent } from './users/new-specialist/pages/welcome-page/welcome-page.component';
import { ContactInfoComponent } from './users/new-specialist/pages/contact-info/contact-info.component';
import { ConfirmationCodeComponent } from './users/new-specialist/pages/confirmation-code/confirmation-code.component';


const routes: Routes = [
  { path: '', component: LandingpageComponent },
  { path: 'specialists', component: BookerLandingComponent },
  { path: 'specialists/new', component: NewSpecialistComponent, children: [
    { path: '', component: WelcomePageComponent, pathMatch: 'full' },
    { path: 'contact-details', component: ContactInfoComponent },
    { path: 'confirmation-code', component: ConfirmationCodeComponent }
  ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
