import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';

type UserFields = 'name' | 'lastName' | 'email' | 'phoneNumber' | 'password';
type FormErrors = { [u in UserFields]: string };

@Component({
  selector: 'app-contact-info',
  templateUrl: './contact-info.component.html',
  styleUrls: ['./contact-info.component.scss']
})
export class ContactInfoComponent implements OnInit {

  userForm: FormGroup;
  formErrors: FormErrors = {
    'name': '',
    'lastName': '',
    'email': '',
    'phoneNumber': '',
    'password': ''
  };
  validationMessage = {
    'name': {
      'required': 'Name is required',
      'minlength': 'Name must be at least 2 char long.'
    },
    'lastName': {
      'required': 'Name is required',
      'minlength': 'Name must be at least 2 char long.'
    },
    'email': {
      'required': 'Email is required',
      'email': 'Email must be valid'
    },
    'phoneNumber': {
      'required': 'Phone number is required',
      'min': 'Phone number must be valid'
    },
    'password': {
      'required': 'Password is required',
      'pattern': 'Password must contain at least one letter and number.',
      'minlength': 'Password must be at least 6 char long.',
      'maslength': 'Password cannot be more than 40 chars long.'
    }
  };

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.buildForm();
  }

  signUp() {
    console.log('test');
  }

  buildForm() {
    this.userForm = this.formBuilder.group(
      {
        'name': ['', [
          Validators.required,
          Validators.minLength(2)
        ]],
        'lastName': ['', [
          Validators.required,
          Validators.minLength(2)
        ]],
        'email': ['', [
          Validators.required,
          Validators.email
        ]],
        'phoneNumber': ['', [
          Validators.required,
          Validators.min(100000)
        ]],
        'password': ['', [
          Validators.pattern('^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+)$'),
          Validators.minLength(6),
          Validators.maxLength(40),
          Validators.required
        ]]
      }
    );
    this.userForm.valueChanges.subscribe(
      data => this.onValueChanged(data)
    );
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.userForm) { return; }

    const form = this.userForm;

    for (const field in this.formErrors) {
      if (Object.prototype.hasOwnProperty.call(this.formErrors, field)) {
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessage[field];
          if (control.errors) {
            for (const key in control.errors) {
              if (Object.prototype.hasOwnProperty.call(control.errors, key)) {
                this.formErrors[field] += `${(messages as {[key: string]: string})[key]} \n`;
              }
            }
          }
        }
      }
    }
  }

}
